<template>
  <el-container>
    <el-aside width="200px">
      <menu-item
        v-for="item in menuList"
        :key="item.title"
        @change="changeInfo(item)"
      >
        {{ item.title }}
      </menu-item>
    </el-aside>
    <el-main>
      <div v-for="(item, index) in infoList" :key="index">
        <jc-text-container v-if="item.type == 'text'">
          <template slot="title">{{ item.title }}</template>
          {{ item.des }}
        </jc-text-container>
        <jc-code v-if="item.type == 'el'" :code="item"></jc-code>
        <jc-table v-if="item.type == 'table'" :code="item"></jc-table>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import MenuItem from "../components/menuItem.vue";
import JcTextContainer from "../components/text.vue";
import JcCode from "../components/code.vue";
import JcTable from "../components/jcTable.vue";
export default {
  components: {
    JcTextContainer,
    MenuItem,
    JcCode,
    JcTable
  },
  data() {
    return {
      menuList: require("../pages/index.js").default,
      infoList: []
    };
  },
  created() {
    this.infoList = this.menuList[0].list;
  },
  methods: {
    changeInfo(item) {
      this.infoList = item.list;
    }
  }
};
</script>

<style scoped>
.el-aside {
  padding: 20px 0px;
  background: #606266;
}
.el-main {
  height: calc(100vh - 60px);
}
</style>



