<template>
  <div class="jc-table">
    <h3>{{ code.title }}</h3>
    <h5>{{ code.des }}</h5>
    <el-table style="width:100%" :data="code.data" border>
      <el-table-column prop="prop" label="属性名" width="150">
      </el-table-column>
      <el-table-column prop="type" label="类型" width="150" align="center">
        <template slot-scope="scope">
          {{ scope.row.type || "String" }}
        </template>
      </el-table-column>
      <el-table-column
        prop="defaultValue"
        label="默认值"
        align="center"
        width="150"
      >
        <template slot-scope="scope">
          {{ scope.row.defaultValue || "----" }}
        </template>
      </el-table-column>
      <el-table-column prop="required" label="必填" align="center" width="120">
        <template slot-scope="scope">
          {{ scope.row.required || "false" }}
        </template>
      </el-table-column>
      <el-table-column prop="message" label="说明" align="center">
        <template slot-scope="scope">
          {{ scope.row.message || "----" }}
        </template>
      </el-table-column>
      <el-table-column prop="des" label="备注" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    code: {
      type: Object
    }
  }
};
</script>

<style scope>
.jc-table {
  margin-top: 12px;
  padding: 12px;
  background: #ffffff;
  border: #ebeef5 1px solid;
  display: flex;
  flex-direction: column;
  text-align: left;
}
h3 h5 {
  margin: 0;
}
</style>



