<template>
  <div id="app">
    <el-container style="height: 100%">
      <el-header>
        <el-button class="header-title" type="text" @click="pushToHome">
          建采网PC端组件文档
        </el-button>
        <el-button type="text" @click="pushToFiled">
          前端规范文档
        </el-button>
      </el-header>
      <router-view />
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  methods: {
    pushToHome() {
      this.$router.push("/");
    },
    pushToFiled() {
      this.$router.push("/standard");
    }
  }
};
</script>

<style>
html,
body {
  height: 100%;
  width: 100%;
  border: hidden;
  /* overflow: hidden; */
  padding: 0;
  margin: 0;
  color: var(--textPrimaryColor);
  font-family: "PingFangSC-Regular", PingFang SC;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 10px 0px 0px 0px;
}
.el-header {
  display: flex;
  text-align: left;
  align-items: center;
  font-size: 24px;
  color: #409eff;
  border-bottom: 1px solid #dcdfe6;
  justify-content: space-between;
}
.header-title {
  font-size: 24px;
}
</style>



