import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/home";
import StandardIndex from "@/views/standard/StandardIndex";
Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/standard",
      name: "Standard",
      component: StandardIndex
    }
  ]
});



