<template>
  <div class="code-view">
    <h3>{{ code.title }}</h3>
    <render-fun :json="code"></render-fun>
    <transition name="code-view">
      <div v-if="showCode">
        {{ code.des }}
        <prism-editor
          class="my-editor"
          v-model="code.html"
          :line-numbers="true"
          :highlight="highlighter"
          :languages="'html'"
        >
        </prism-editor>
      </div>
    </transition>

    <div class="show-code" @click="showCode = !showCode">
      {{ showCode ? "关闭" : "展开" }}示例代码
    </div>
  </div>
</template>

<script>
import RenderFun from "./render";
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css";
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism-tomorrow.css";
export default {
  components: {
    RenderFun,
    PrismEditor
  },
  props: {
    code: {
      type: Object
    }
  },
  data() {
    return {
      showCode: false
    };
  },
  methods: {
    highlighter(code) {
      return highlight(code, languages.js);
    }
  }
};
</script>

<style scoped>
.my-editor {
  background: #2d2d2d;
  color: #ccc;
  margin-top: 12px;
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  width: 90%;
  padding: 5px;
  margin-top: 12px;
}

/* optional class for removing the outline */
.prism-editor__textarea:focus {
  outline: none;
}
.code-view {
  margin-top: 12px;
  padding: 12px;
  background: #ffffff;
  border: #ebeef5 1px solid;
  display: flex;
  flex-direction: column;
  text-align: left;
}
.show-code {
  margin-top: 12px;
  padding: 12px;
  background: #ffffff;
  border: #ebeef5 1px solid;
  display: flex;
  flex-direction: column;
  text-align: center;
  cursor: pointer;
}
.code-view-enter-active {
  transition: opacity 0.5s;
}
.code-view-enter {
  opacity: 0;
}
.code-view-leave-active {
  transition: opacity 0.5s;
}
.code-view-leave-to {
  opacity: 0;
}
</style>



