import Vue from "vue";
function click() {
  console.log("点击了按钮1");
}
export default {
  functional: true,
  props: {
    json: Object
  },
  render(h, ctx) {
    let { json } = ctx.props;
    const com = Vue.extend({
      template: `<div>${json.html}</div>`,
      methods: json.methods
    });
    return h(com, {});
  }
};



