<template>
  <div class="file-view">
    <el-table style="width:100%" :data="tableDataList" border>
      <el-table-column
        label="序号"
        width="60"
        type="index"
        align="center"
      ></el-table-column>
      <el-table-column label="名称" prop="name"></el-table-column>
      <el-table-column label="类型" prop="type" width="120" align="center">
      </el-table-column>
      <el-table-column
        label="创建日期"
        prop="createDate"
        width="150"
        align="center"
      >
      </el-table-column>
      <el-table-column
        label="创建人"
        prop="createPerson"
        width="150"
        align="center"
      >
      </el-table-column>
      <el-table-column label="下载" prop="url" width="120" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="downloadFiled(scope.row)">
            点击下载
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableDataList: [
        {
          name: "前端代码规范(PC端Vue2.x)",
          type: "pdf",
          createDate: "2023年2月13日",
          createPerson: "董柳兴",
          url:
            "https:/raw.githubusercontent.com/cywuxian/cywuxian.github.io/master/前端代码规范(PC端Vue2.x).pdf"
        },
        {
          name: "前端组件库管理规范",
          type: "pdf",
          createDate: "2023年2月13日",
          createPerson: "董柳兴",
          url:
            "https:/raw.githubusercontent.com/cywuxian/cywuxian.github.io/master/前端组件库管理规范.pdf"
        }
      ]
    };
  },
  methods: {
    downloadFiled(row) {
      window.open(row.url);
    }
  }
};
</script>

<style scoped>
.file-view {
  margin: 12px 120px 0px 120px;
}
</style>



